import React from 'react'
import { Box, Grid, Grid2, Typography, Accordion, AccordionSummary, AccordionDetails, Container, Link } from '@mui/material'
import { useUser } from '../../Context/Provider'
import Carrusel from '../../Components/Carrusel/Carrusel'
import CategorySelector from '../../Components/CategorySelector/CategorySelector'
import useDashboard from './useDashboard'
import CardCarrusel from '../../Components/Carrusel/CardCarrusel/CardCarrusel'
import CardBusiness from '../../Components/Card/CardBusiness/CardBusiness'
import ExpandMoreIcon from '@mui/icons-material/ExpandMore'
import LoadingPage from '../../Components/LoadingPage/LoadingPage'
import Alerts from '../../Components/Alerts/Alert'
import "../../Resources/Css/Dashboard.css"

const Dashboard = () => {
  const { state, features } = useDashboard()
  const { dataContext } = useUser()

  return (
    <>
      {/* Pantalla de carga */}
      {state.isLoading && ( <LoadingPage /> )}

      {/* Alert para errores */}
      {state.success && (
        <Alerts 
          prop={{severity: "success", title: "Completado", alert: state.success}}  
          closeFunction={state.setSuccess} 
          params={null}
        /> 
      )}
      {state.warning && (
        <Alerts 
          prop={{severity: "warning", title: "Advertencia", alert: state.warning}}  
          closeFunction={state.setWarning} 
          params={null}
        /> 
      )}
      {state.error && (
        <Alerts 
          prop={{severity: "error", title: "Error", alert: state.error}}  
          closeFunction={state.setError} 
          params={null}
        /> 
      )}
      <Box mb={4}><Carrusel /></Box>   

      <Box my={4}>
        <Typography variant="h4" align="center" gutterBottom sx={{ fontWeight: 'bold', color: '#00004e', my: 2 }}>Encontrá tu marca preferida</Typography>
        <CardBusiness products={dataContext} customBreakpoints={{
          0: { slidesPerView: 1 },
          770: { slidesPerView: 2 },
          1120: { slidesPerView: 3 },
          1440: { slidesPerView: 4 }
        }}/>
      </Box>

      <Box my={4} px={2}>
        <Typography variant="h4" align="center" gutterBottom sx={{ fontWeight: 'bold', color: '#00004e', my: 2 }}>Categorias</Typography>
        <CategorySelector/>
      </Box>

      <Box mt={6}>
        <Box
          sx={{
            position: 'relative',
            width: '100%',
            height: { xs: 300, md: 600 },
            backgroundImage: 'url(https://picsum.photos/1920/600?random=0)',
            backgroundSize: 'cover',
            backgroundPosition: 'center',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            color: 'white',
            textAlign: 'center'
          }}>

          <Box
            sx={{
              position: 'absolute',
              inset: 0,
              backgroundColor: 'rgba(0,0,0,0.4)'
            }}/>

          <Typography
            variant="h4"
            sx={{
              position: 'relative',
              zIndex: 1,
              textShadow: '0 0 10px rgba(0,0,0,0.8)',
              fontSize: { xs: '1.2rem', sm: '1.5rem', md: '2rem' },
              px: 2 
            }}>

            ¡Bienvenido Jose a GlobalComputer! Tu tecnología al mejor precio
          </Typography>
        </Box>
      </Box>

      <Box className="features-container">
        <Grid2 container spacing={10} justifyContent="center">
          {features.map((feature, index) => (
            <Grid2 item xs={12} sm={6} md={4} key={index} className="feature-item">
              <Box className="feature-icon">{feature.icon}</Box>
              <Typography variant="h6" className="feature-title">
                {feature.title}
              </Typography>
              <Typography variant="body2" className="feature-description">
                {feature.description}
              </Typography>
            </Grid2>
          ))}
        </Grid2>
      </Box>

      <Box my={4}>
        <Typography variant="h4" align="center" gutterBottom sx={{ fontWeight: 'bold', color: '#00004e', my: 2 }}>Top 10 mas vendidos</Typography>
        <CardCarrusel cards={dataContext} 
          setIsLoading={state.setIsLoading} 
          setSuccess={state.setSuccess} 
          setError={state.setError}
          customBreakpoints={{
            0: { slidesPerView: 1 },
            770: { slidesPerView: 2 },
            1120: { slidesPerView: 3 },
            1440: { slidesPerView: 4 },
            1850: { slidesPerView: 5 },
          }}/>
      </Box>

      <Box my={4}>
        <Typography variant="h4" align="center" gutterBottom sx={{ fontWeight: 'bold', color: '#00004e', my: 2 }}>En oferta</Typography>
        <CardCarrusel 
          cards={dataContext}
          setIsLoading={state.setIsLoading} 
          setSuccess={state.setSuccess} 
          setError={state.setError}
          customBreakpoints={{
            0: { slidesPerView: 1 },
            770: { slidesPerView: 2 },
            1120: { slidesPerView: 3 },
            1440: { slidesPerView: 4 },
            1850: { slidesPerView: 5 },
          }} />
      </Box>
      
      <Container maxWidth="md" sx={{ mt: 4, mb: 4 }}>
        <Typography variant="h4" align="center" gutterBottom sx={{ fontWeight: 'bold', color: '#00004e' }}>Preguntas Frecuentes</Typography>
        <Box mt={2}>
          <Accordion>
            <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel1-content" id="panel1-header">
              <Typography variant="h6">¿Qué servicios ofrecen?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Ofrecemos servicios de ecommerce, venta de productos de informática, 
                cursos especializados y soporte técnico para notebooks.
              </Typography>
            </AccordionDetails>
          </Accordion>

          <Accordion>
            <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel2-content" id="panel2-header">
              <Typography variant="h6">¿Cómo puedo realizar una compra?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Puedes realizar tu compra a través de nuestro sitio web agregando productos 
                al carrito y completando el proceso de pago de forma segura.
              </Typography>
            </AccordionDetails>
          </Accordion>

          <Accordion>
            <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel3-content" id="panel3-header">
              <Typography variant="h6">¿Tienen servicio de soporte técnico?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Sí, ofrecemos soporte técnico para notebooks. Puedes agendar un turno a 
                través de nuestro sistema online para recibir atención personalizada.
              </Typography>
            </AccordionDetails>
          </Accordion>
        </Box>
      </Container>

      <Grid container spacing={2} my={4} sx={{ px: { xs: 2, md: 40 } }}>
        <Grid item xs={12} md={6}>
          <Box
            sx={{
              border: '1px solid #e0e0e0',
              borderRadius: 2,
              p: 2,
              textAlign: 'center',
            }}>

            <Typography variant="body2" color="textSecondary">
              ¿Te arrepentiste de comprar?
            </Typography>
            <Link
              href="#"
              underline="hover"
              sx={{ fontSize: '0.9rem', display: 'block', mt: 1 }}>

              Cómo cancelar una compra
            </Link>
          </Box>
        </Grid>

        <Grid item xs={12} md={6}>
          <Box
            sx={{
              border: '1px solid #e0e0e0',
              borderRadius: 2,
              p: 2,
              textAlign: 'center',
            }}>

            <Typography variant="body2" color="textSecondary">
              ¿Conocés las normas que protegen tus compras?
            </Typography>
            <Link
              href="https://www.argentina.gob.ar/produccion/defensadelconsumidor/ley"
              target="_blank"
              rel="noopener"
              underline="hover"
              sx={{ fontSize: '0.9rem', display: 'block', mt: 1 }}>

              Contratos de adhesión - Ley N° 24.240 de Defensa del Consumidor
            </Link>
          </Box>
        </Grid>
      </Grid>
    </>
  )
}

export default Dashboard
