import React from "react";
import { Box, Card, CardContent, CardMedia, Typography } from "@mui/material";
import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination, Navigation, Autoplay } from "swiper/modules";
import { useCardBusiness } from "./useCardBusiness";
import { useNavigate } from 'react-router-dom'
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';

const CardBusiness = ({ products, customBreakpoints }) => {
  const { brands } = useCardBusiness(products);
  const navigate = useNavigate()

  return (
    <Box
      sx={{
        width: '100%',
        display: 'flex',
        justifyContent: 'center',
        '& .swiper': { width: '100%' },
        '& .swiper-pagination': { mt: 0 },
        '& .swiper-button-next, & .swiper-button-prev': {
            color: '#000',
            backgroundColor: '#fff',
            borderRadius: '50%',
            width: 40,
            height: 40,
            boxShadow: 3,
            '&::after': {
                fontSize: '15px',
                fontWeight: 'bold',
            }
        }
      }}>

      <Swiper
        modules={[Pagination, Navigation, Autoplay]}
        pagination={{ clickable: true }}
        navigation={true}
        autoplay={{
          delay: 5000,
          disableOnInteraction: false
        }}
        slidesPerGroup={1}
        breakpoints={customBreakpoints}
        spaceBetween={16}
        style={{ paddingBottom: '32px' }}>
          
        {brands.map((brand, index) => (
          <SwiperSlide key={index}>
            <Card
              sx={{
                width: { xs: 345, md: 430 },
                height: 600,
                mx: 'auto',
                borderRadius: 2,
                boxShadow: 3,
                cursor: 'pointer',
                position: 'relative',
                overflow: 'hidden',
                transition: 'transform 0.3s',
                "&:hover": {
                  transform: 'scale(1.03)',
                },
              }}
              
              onClick={() => navigate(`/home/products/brand/${brand.name}`)}>

              <CardMedia
                component="img"
                height="430"
                image={brand.image}
                alt={brand.name}
                sx={{ objectFit: "cover" }}
              />

              <Box sx={{
                  position: 'absolute',
                  top: '360px',
                  left: '50%',
                  transform: 'translateX(-50%)',
                  width: 120,
                  height: 120,
                  borderRadius: '50%',
                  backgroundColor: '#fff',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  overflow: 'hidden',
                  boxShadow: 3,
                  border: '2px solid #ccc',
                }}>

                <Box component="img"
                  src={`https://picsum.photos/80/80?random=${index + 50}`}
                  alt={`${brand.name} logo`}
                  style={{ width: '100%', height: '100%', objectFit: 'cover' }}
                />
              </Box>
              <CardContent sx={{ textAlign: "center", mt: 5 }}>
                <Typography variant="h6" sx={{ fontWeight: "bold", mt: 1 }}>
                  {brand.name}
                </Typography>
              </CardContent>
            </Card>
          </SwiperSlide>
        ))}
      </Swiper>
    </Box>
  );
};

export default CardBusiness;
