import React from 'react';
import { Grid, Card, CardActionArea, Typography, Box } from '@mui/material';
import useCategorySelector from './useCategorySelector';

const CategorySelector = () => {
  const { categories, handleSelect } = useCategorySelector();

  return (
    <Grid container spacing={2} justifyContent="center">
      {categories.map((cat) => (
        <Grid item xs={6} sm={4} md={3} key={cat.name}>
          <Card sx={{ height: 200, position: 'relative' }}>
            <CardActionArea onClick={() => handleSelect(cat.name)} sx={{ height: '100%' }}>
              <Box
                sx={{
                  height: '100%',
                  position: 'relative',
                  overflow: 'hidden',
                  '&::before': {
                    content: '""',
                    position: 'absolute',
                    inset: 0,
                    backgroundColor: 'rgba(0,0,0,0.4)',
                    transition: 'background-color 0.3s ease',
                    zIndex: 1,
                  },
                  '&:hover::before': {
                    backgroundColor: 'rgba(0,0,0,0.65)',
                  },
                  backgroundImage: `url(${cat.image})`,
                  backgroundSize: 'cover',
                  backgroundPosition: 'center',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  color: 'white',
                  textShadow: '0 0 10px rgba(0,0,0,0.9)',
                  fontWeight: 'bold',
                  fontSize: '1.2rem',
                }}
              >
                <Typography
                  variant="h6"
                  sx={{
                    position: 'relative',
                    zIndex: 2,
                    transition: 'transform 0.3s ease, text-shadow 0.3s ease',
                    '&:hover': {
                      transform: 'scale(1.05)',
                      textShadow: '0 0 15px rgba(255,255,255,0.9)',
                    },
                  }}
                >
                  {cat.name}
                </Typography>
              </Box>
            </CardActionArea>
          </Card>
        </Grid>
      ))}
    </Grid>
  );
};

export default CategorySelector;
