import React, { useState } from 'react'
import LocalShippingIcon from '@mui/icons-material/LocalShipping'
import CreditCardIcon from '@mui/icons-material/CreditCard'
import InventoryIcon from '@mui/icons-material/Inventory'

const useDashboard = () => {
    const [isLoading, setIsLoading] = useState(false)
    const [success, setSuccess] = useState(null)
    const [warning, setWarning] = useState(null)
    const [error, setError] = useState(null)
    const features = [
        {
          icon: <LocalShippingIcon style={{ fontSize: 100 }} />,
          title: "Aprovechá el beneficio del envío gratis",
          description: "Aplica en compras a partir de $150,000. Sumá todo lo que quieras al carrito.",
        },
        {
          icon: <CreditCardIcon style={{ fontSize: 100 }} />,
          title: "Elegí tu medio de pago favorito",
          description: "Pagá con tarjeta o en efectivo. Tu dinero está protegido con Mercado Pago.",
        },
        {
          icon: <InventoryIcon style={{ fontSize: 100 }} />,
          title: "Recibí tus productos en menos de 48 hs",
          description: "Tus paquetes están seguros. Tenés el respaldo de los envíos con Mercado Libre.",
        },
    ]

    const state = {
      isLoading,
      setIsLoading,
      success,
      setSuccess,
      warning,
      setWarning,
      error,
      setError
    }
    
    return { state, features }
}

export default useDashboard