import { useNavigate } from 'react-router-dom';

const useCategorySelector = () => {
  const navigate = useNavigate();

  const categories = [
    { name: 'Mouse', image: 'https://picsum.photos/345/200?random=0' },
    { name: 'Teclado', image: 'https://picsum.photos/345/200?random=1' },
    { name: 'Periféricos', image: 'https://picsum.photos/345/200?random=2' },
    { name: 'Componentes', image: 'https://picsum.photos/345/200?random=3' },
    // Podés agregar más
  ];

  const handleSelect = (category) => {
    navigate(`/home/products/category/${category}`)
  };

  return { categories, handleSelect };
};

export default useCategorySelector